
#playlists section div.title {
  cursor: pointer;
}

#playlists section div.title h2 {
  font-size: 14px;
  display: inline-block;
  padding-right: 5px;
}
#playlists section div.title h2.active {
  color: lightblue;
}
#playlists section div.title i.active {
  color: lightblue;
}

#playlists section ul {
  display: none;
  padding: 0;
  list-style-type: none;
}
#playlists section ul.showPlaylist {
  display: block;
}

#playlists section ul li {
  cursor: pointer;
}

#playlists section ul li h3 {
  font-size: 12px;
  display: inline-block;
  padding-right: 8px;
}

#playlists section ul li h3.active {
  color: lightcoral;
}
#playlists section ul li i.active {
  color: lightcoral;
}

#playlists section ul li iframe {
  display: none;
}
#playlists section ul li.showVideo iframe {
  display: block;
}
  